<template>
  <div id="body" @mouseenter="show" @mouseleave="hide()">
    <audio preload muted loop :src="url" ref="audio" id="audio"> audio </audio>
    <div class="player-btn" @click="playControl" v-if="isHide===false">
      <img v-if="isHide===false&&isplay===false" src="../assets/play.png" alt />
      <img v-if="isHide===false&&isplay===true" src="../assets/pause.png" alt />
      <img v-if="isHide===true" src="../assets/backward.png" title="" alt />
    </div>
    <div class="show-btn" @click="show" v-if="isHide===true">
      <img src="../assets/backward.png" title="" alt />
    </div>

    <div class="player-body">
      <div class="msg" ref="msg">
        <p :key="1">{{song}}</p>
        <p :key="2">{{singer}}</p>
        <p :key="3">{{cur}}%</p>
      </div>

      <div class="controller">
        <div class="progess">
          <ProgressBar
            :init="init"
            @valChange="valChange"
            @moveDown="moveDown"
            :cur="currentTime"
            @moving="moving"
          ></ProgressBar>
        </div>
        <div class="volume" @click="isTrunOn">
          <img v-if="isVolume===true" src="../assets/quieter.png" alt title="" />
          <img v-else src="../assets/turn_off.png" title="" alt />
        </div>
        <div class="next" @click="next">
          <img src="../assets/next.png" title="" alt />
        </div>
        <div class="list">
          <img src="../assets/quarter_note.png" title="" alt />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import ProgressBar from "./ProgressBar";
export default {
  //import

  components: {
    ProgressBar
  },
  data() {
    //
    return {
      // 
      time: null,
      url:
        "https://m7.music.126.net/20191012191036/7b85b322c7e39024b3d1a9fe0ef494cd/ymusic/5258/0f5f/015c/e23eb784398544031837660e6d233a6e.mp3",
      song: "",
      singer: "",
      //
      init: 0, //
      cur: 0, //
      currentTime: 0, //
      //false
      isplay: false,
      //
      isMoveDown: null,
      //
      timer: null,
      //
      isMoving: null,
      //
      isVolume: true,
      //
      isHide: false,
      //
      musicList: [],
      //
      curIndex: 0
    };
  },
  // data
  computed: {},
  //data
  watch: {
    //
    isMoveDown() {
      if (this.isMoveDown) {
        this.toTarget();
        this.isMoveDown = null;
      }
    },
    //
    isMoving() {
      if (this.isMoving) {
        this.stopFllow();
      }
      this.isMoving = null;
    },
    //
    async cur(val) {
      this.currentTime = await val;
      if (this.isplay === false) {
        audio.play();
        this.isplay = true;
        //
        this.time = Math.floor(audio.duration);
        //
        audio.currentTime = (Math.floor(this.cur) / 100) * this.time;
        this.follow();
      }
    },
    //
    async curIndex(val) {
      await this.getMusic(val);
      if (this.isplay) {
        audio.play();
      } else {
        // this.isplay = false;
        // this.stopFllow();
        // this.playControl()
      }
    }
  },
  //
  methods: {
    //props
    valChange(val) {
      this.cur = val;
    },
    //or
    playControl() {
      let audio = this.$refs.audio;
      //false
      if (this.isplay === false) {
        audio.play();
        this.isplay = true;
        //
        this.time = Math.floor(audio.duration);
        //
        audio.currentTime = (Math.floor(this.cur) / 100) * this.time;
        this.follow();
      } else {
        audio.pause();
        this.isplay = false;
        this.stopFllow();
      }
    },
    //
    async toTarget() {
      await this.stopFllow();
      let audio = this.$refs.audio;
      audio.currentTime = (Math.floor(this.cur) / 100) * this.time;
      this.follow();
    },
    //
    moveDown(data) {
      this.isMoveDown = data;
    },
    //
    moving(data) {
      this.isMoving = data;
    },
    //
    follow() {
      let _this = this;
      let curTime;
      let audio = this.$refs.audio;
      if (this.timer == null) {
        this.timer = setInterval(function() {
          curTime = audio.currentTime; //
          _this.currentTime = Math.floor((curTime / audio.duration) * 100);
        }, 0);
      }
    },
    //
    stopFllow() {
      clearInterval(this.timer);
      this.timer = null;
    },
    //
    isTrunOn() {
      let audio = this.$refs.audio;
      //false
      if (this.isVolume) {
        (audio.volume = 0), (this.isVolume = false);
      } else {
        (audio.volume = 1), (this.isVolume = true);
      }
    },
    //
    hide() {
      document.getElementById("body").style.right = "-40rem";
      this.isHide = true;
    },
    //
    show() {
      document.getElementById("body").style.right = "0";
      this.isHide = false;
    },
    //
    next() {
      if (this.curIndex < this.musicList.length - 1) {
        this.curIndex++;
      } else {
        this.curIndex = 0;
      }
    },
    //
    async getMusic(index) {
      let id = this.musicList[index].id;
      //get
      const res = await this.$http.get(
        `https://api.imjad.cn/cloudmusic/?type=song&id=` + id + `&br=320000`
      );
      this.url = res.data.data[0].url;
      this.song = this.musicList[index].song;
      this.singer = this.musicList[index].singer;
    },

    async fetchList() {
      const res = await this.$http.get("musics/list");
      this.musicList = res.data;
      this.$nextTick(function() {
        this.getMusic(0);
      });
    }
  },
  // - this
  created() {
    this.fetchList();
  },
  // - DOM
  mounted() {
    this.hide();
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
#body {
  width: 46rem;
  height: 8rem;
  position: fixed;
  top: 6rem;
  /* right: -36rem; */
  right: 0;
  user-select: none;
  background: rgba(255, 255, 255, 0.8);
  transition: 0.8s;
}
#body:hover {
  background: rgba(255, 255, 255, 1);
}
.player-btn {
  float: left;
  width: 20%;
  height: 100%;
  background-color: rgb(65, 184, 131);
}
.player-btn img {
  max-width: 40%;
  position: relative;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
}
.player-body {
  display: inline-block;
  width: 80%;
  height: 100%;
}
.msg {
  width: 90%;
  height: 80%;
  margin: 0 5%;
  text-align: center;
  display: inline-block;
}
.msg > p {
  font-size: 1.2rem;
  margin-top: 0.4rem;
}
.controller {
  display: inline-block;
  height: 20%;
  width: 100%;
  padding: 0 10%;
}
.progess {
  display: inline-block;
  width: 70%;
  height: 100%;
}
.list {
  display: inline-block;
  height: 100%;
  width: 10%;
}
.list img {
  max-width: 100%;
  position: relative;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
}
.list img:hover{
  min-width: 110%;
}
.next {
  display: inline-block;
  height: 100%;
  width: 10%;
}
.next img {
  max-width: 100%;
  position: relative;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
}
.next img:hover{
  min-width: 110%;
}
.volume {
  display: inline-block;
  height: 100%;
  width: 10%;
}
.volume img {
  max-width: 100%;
  position: relative;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
}
.volume img:hover{
  min-width: 110%;
}
.show-btn {
  float: left;
  width: 10%;
  height: 100%;
  background-color: rgb(65, 184, 131);
}
.show-btn img {
  max-width: 40%;
  position: relative;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
}

@media screen and (max-width: 1000px) {
  #body {
    display: none;
  }
}
</style>