<!--  -->
<template>
  <div class="bar-body" ref="process">
    <div class="tunk">
      <!--  -->
      <div class="process" :style="{width}"></div>
      <!--  -->
      <div class="contact" ref="contact" :style="{'left':left}"></div>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  props: {
    init: Number, //
    cur: Number //
  },
  data() {
    //
    return {
      min: 0,
      max: 100,
      process: null, //DOM
      contact: null, //DOM
      value: this.init, //
      preX: null //
    };
  },
  // data
  computed: {
    // slidertrunkleft
    //   -/- = sliderwidth / sliderwidth
    // trunk left =  sliderwidth + trunk/2
    scale() {
      return (this.value - this.min) / (this.max - this.min);
    },
    //
    width() {
      if (this.process) {
        return this.process.offsetWidth * this.scale + "px";
      } else {
        return 0;
      }
    },
    //
    left() {
      if (this.process) {
        return (
          this.process.offsetWidth * this.scale -
          this.contact.offsetWidth / 2 +
          "px"
        );
      } else {
        return 0;
      }
    }
  },
  //data
  watch: {
    //
    value(val) {
      this.$emit("valChange", val);
    },
    //
    cur(val) {
      this.value = val;
    }
  },
  //
  methods: {},
  // - this
  created() {},
  // - DOM
  mounted() {
    //   
    this.process = this.$refs.process;
    this.contact = this.$refs.contact;

    let _this = this;
    this.preX = this.contact.getBoundingClientRect().x;
    this.process.onmousedown = function(e) {
      let preX = parseInt(_this.preX);
      let disX = parseInt(e.clientX);
      let newWidth = disX - preX < 0 ? preX - disX : disX - preX;
      let scale = newWidth / _this.process.offsetWidth;
      _this.value = Math.ceil((_this.max - _this.min) * scale + _this.min);
      _this.value = Math.max(_this.value, _this.min);
      _this.value = Math.min(_this.value, _this.max);
      _this.$emit("moveDown", true);
    };
    //
    //
    this.contact.onmousedown = function(e) {
      _this.$emit("moving", true);
      let width = parseInt(_this.width);
      // console.log("width=" + width);
      //X
      let disX = e.clientX;
      // console.log("X=" + disX);
      //
      document.onmousemove = function(e) {
        // value, left, width
        // valueleftwidth
        // width
        let newWidth = e.clientX - disX + width;
        // console.log("width=" + newWidth);
        // 
        let scale = newWidth / _this.process.offsetWidth;
        // console.log(":" + scale);
        _this.value = Math.ceil((_this.max - _this.min) * scale + _this.min);
        _this.value = Math.max(_this.value, _this.min);
        _this.value = Math.min(_this.value, _this.max);

        _this.$emit("moving", true);
      };
      //
      document.onmouseup = function() {
        document.onmousemove = document.onmouseup = null;
        _this.$emit("moveDown", true);
        _this.$emit("moving", null);
      };
      return false;
    };
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
.bar-body {
  width: 90%;
  height: 100%;
  /* margin: 0 5%; */
}
.tunk {
  position: relative;
  height: 40%;
  top: 30%;
  background: #dadada;
}
.process {
  position: absolute;
  left: 0;
  /* width: 100%; */
  height: 100%;
  /* top: 30%; */
  background: rgb(65, 184, 131);
}
.contact {
  position: absolute;
  /* left: 10%; */
  width: 2rem;
  height: 2rem;
  line-height: 2rem;
  border-radius: 50%;
  background: #ffffff;
  cursor: pointer;
  top: -100%;
  border: 0.1rem solid rgb(65, 184, 131);
}
.contact:hover {
  background: rgb(65, 184, 131);
  box-shadow: 0.2rem 0.2rem 0.8rem #888;
}
</style>